import react from "react";
import { NavLink } from "react-router-dom";
import web from "../src/img/illustration-1.svg"
const Comman = (props) => {
    return (
        <>

            <section id="header" className="d-flex align-items-center">
                <div className="container-fluid ">
                    <div className='row'>
                        <div className="col-10 mx-auto">
                            <div className="row">
                                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                                    <h1>{props.title}<strong className="brand_name text-primary">Kurm Infotech.</strong></h1>
                                    <h2 className="my-3 text-dark fs-5">
                                        We are the team of talented developer making Websites
                                    </h2>
                                    <div className="mt-3">
                                        <NavLink to={props.visit} className="btn btn-outline-primary">{props.btnname}</NavLink>
                                    </div>
                                </div>
                                <div className="col-md-6 order-1 order-lg-2 header-img">
                                    <img src={props.imgsrc} className="img-fluid animated" atl="home img" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}


export default Comman;